<template>
  <v-app id="inspire">
    <nprogress-container></nprogress-container>
    <v-main class="grey lighten-4">
      <v-container>
            <v-row align="center" justify="center" class="mb-15">

            <v-col
                sm="4"
                xs="4"
                md="4"
                lg="4"
                xl="4"
                class="pt-6"

            >
                <div
                min-height="30vh"
                rounded="lg"
                >
                 <template>
                    <v-flex sm12 md10 offset-md1 class=" pt-xs-2 mt-xs-2 pt-sm-4 mt-sm-4 pt-md-8 mt-md-8 pt-4 mt-4" >
                    <v-card
                    elevation="4" light
                    outlined
                    >

                        <v-progress-linear
                        :active="dialogW"
                        indeterminate
                        color="primary"
                        ></v-progress-linear>
                        <v-card-title
                        class="red darken-2"
                        dark>
                            <v-layout align-center justify-space-between>
                            <h3 class="text-xs white--text">
                                {{ platformName }}
                            </h3>
                                <v-img :alt="platformName" class="ml-3" contain height="48px" width="48px" position="center right" 
                                :src="$baseUrl+'storage/icons/m-pesa.png'"
                                :lazy-src="$baseUrl+'storage/icons/m-pesa.png'"
                                ></v-img>
                            </v-layout>
                        </v-card-title>
                        <v-divider></v-divider>
                        <v-card-text class="pt-0">
                            <v-form ref="Form" v-model="valid" lazy-validation @submit.prevent="validate">
                                <v-row align="center" justify="center" class="mt-3" >
                                    <h3 class="text-xs black--text">
                                        Order: {{book.order_id}}
                                    </h3>
                                </v-row>
                                <v-row align="center" justify="center" class="mt-4" >
                                    <h3 class="text-xs black--text">
                                        {{book.alphabetic_code}} {{numberFormat(book.boking_amount)}}
                                    </h3>
                                </v-row>
                                <v-row align="center" justify="center" class="mt-4" >
                                    <h3 class="text-xs green--text">
                                        <div v-if="dialogW">
                                            {{$t('open_your_ussd')}}
                                        </div>
                                    </h3>
                                </v-row>
                                <v-row class="pt-2">
                                    <v-col cols="12">
                                        <v-text-field class="centered-input" dense outlined  :error-messages="error"  v-model="phone_number" :rules="[rulesPhone.required,rulesPhone.min,rulesPhone.max]" :label="$t('phone_number')" minlength="9" maxlength="9" :counter="9" required ></v-text-field>
                                    </v-col>
                                    <v-spacer></v-spacer>
                                    <v-col class="d-flex" cols="12" sm="12" xsm="12" align-end>
                                        <v-btn   block :loading="dialogW" :disabled="dialogW"  color="green darken-2" class="white--text"  type="submit"> {{$t('pay_via_mpesa')}} </v-btn>
                                    </v-col>
                                </v-row>
                            </v-form>
                        </v-card-text>
                        <v-divider></v-divider>

                        <v-card-actions>
                            <lang/>
                        <v-spacer></v-spacer>
                        <v-btn text
                        to="/home"
                        >
                            {{$t('cancel')}}
                        </v-btn>
                        </v-card-actions>

                    </v-card>
                        <v-layout align-center justify-space-between>
                        <p class="caption my-3">
                            <a href="https://app.eatinmore.com/privacy">Privacy Policy</a>
                            |
                            <a href="https://app.eatinmore.com/terms" target="_blank">Terms of Service</a>
                        </p>
                        <p class="caption my-3">Powered by <a href="https://eatinmore.com" target="_blank">EatInMore</a></p>
                        </v-layout>
                    </v-flex>
                    </template>

                </div>
            </v-col>
            </v-row>

      </v-container>
        <v-btn
            fab
            dark
            fixed
            bottom
            right
            color="primary"
            class="mb-0"
            to="/contact"
            small
          >
            <v-icon>mdi-help</v-icon>
        </v-btn>
    </v-main>
  </v-app>
</template>
<script>
  import numeral from 'numeral';
  import NprogressContainer from 'vue-nprogress/src/NprogressContainer'
  import lang from "../Helpers/i18n.vue";
  import moment from 'moment'
  export default {


    components: {
        NprogressContainer,
        lang
    },

    data() {

        return {
            platformName:"Vodacom",
            dialogW:false,
            userInfo: localStorage.getItem('user'),
            rulesPhone: {
                required: value => !!value || "Required.",
                min: v => (v && v.length >= 9) || "Min 9 digits",
                max: v => (v && v.length <= 9) || "Max 9 digits"
            },
            phone_number:'',
            book:'',
            valid:false,
            error:'',
        }

    },

    computed: {
        token(){
            return this.$route.params.token;
        },
    },
    methods: {
        formatDate(date) {
            return moment(date).format('DD-MM-YYYY HH:mm:ss');
        },
        open(url){
            let routeData = this.$router.resolve({path: url});
            var route = routeData.href.replace("pub-order/", "");
            window.open(route, '_blank');

        },
        numberFormat(value){
            return numeral(value).format('0,0')
        },
        onScroll (e) {
        if (typeof window === 'undefined') return
        const top = window.pageYOffset ||   e.target.scrollTop || 0
        this.fab = top > 20
        },
        toTop () {
        this.$vuetify.goTo(0)
        },
        validate() {
        if (this.$refs.Form.validate()) {
            // submit form to server/API here...
            this.dialogW=true;
            this.sendData();
        }
        },
        sendData() {
        this.axios
        .post("check-out-mpesa-mz-pay-now", { data: { phoneNumber: this.phone_number,token:this.token} })
        .then(response => {
            this.allerros = [];
            this.loading = false;
            if (response.data.errors) {
                this.dialogW = false;
                this.dialogW = false;
                //console.log(response.data.errors);
                this.error = response.data.errors[0]
                //response.data.errors.forEach(error => { this.openNotification('error', 'Error', error);});
            } else {
                this.$router.push({ name: 'confirm-payment' });
            }
        })
        .catch((error) => {
            this.dialogW = false;
            this.dialogW = false;
            var errors =null;
            var status=error.response.status;
            //console.log(status);
            if (status == 422){
                errors=error.response.data.errors;
                //console.log(errors[0]);
                this.error = errors[0]
                //errors.forEach(error => { this.openNotification('error', 'Error', error);});
            }else if(error.message){
                this.error = error.message
            }else{
                this.dialogW = false;
                this.dialogW = false;
                this.error = error
                //this.openNotification('error','Error',error);
            }
        });
        },
        openNotification: function (type, m, d) {
            this.$notification.config({
                placement: 'topRight',
                top: 35,
                duration: 8,
            });
            this.$notification[type]({
                message: m,
                description: d,
            });
        },
      
    },
    mounted() {
        this.axios
            .get('pub-order-data/'+this.token)
            .then(response => (
                this.book = response.data,
                this.phone_number = response.data.phone1

            ));
        },

  }
</script>
<style >
.centered-input input {
  text-align: center
}
</style>
